import BaseService from "../baseservice/BaseService";
import CommonsUtils from "../../utils/CommonsUtils";
import Limit from "../../beans/Limit";
import VueContext from "../../context/VueContext";
/**
 * Base class service for crud operations
 */
export default class BaseCrudService extends BaseService {
  constructor(baseHost, pathService) {
    super(baseHost, pathService);
    this.pathEdit = "/edit";
    this.pathAdd = "/add";
    this.pathDelete = "/delete";
    this.pathList = "/list";
    this.pathRead = "/read";
    this.pathLoadNew = "/loadNew";
    this.pathLCount = "/count";
    this.pathLListOnlyFields = "/listOnlyFields";
  }

  /**
   * Method for edit data
   * @param {*} base is base data for edit
   */
  async edit(base) {
    const body = { data: base };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathEdit,
      JSON.stringify(body),
      null
    );
  }

  /**
   * Method for add data
   * @param {*} base is base data for add
   */
  async add(base) {
    const body = { data: base };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathAdd,
      JSON.stringify(body),
      null
    );
  }

  /**
   * Method for delte data
   * @param {*} base is base data for delete
   */
  async delete(base) {
    const body = { data: base };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathDelete,
      JSON.stringify(body),
      null
    );
  }

  /**
   * Method for read data
   * @param {*} base is base data for read
   */
  async read(base) {
    const body = { data: base };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathRead,
      JSON.stringify(body),
      null
    );
  }

  /**
   * Method for load new entity
   */
  async loadNew() {
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathLoadNew,
      JSON.stringify({}),
      null
    );
  }

  /**
   * Method for list data
   * @param {*} filters for list
   * @param {*} fetchs for list
   * @param {*} orders for list
   * @param {*} limits for list
   */
  async list(filters, fetchs, orders, limits) {
    const body = {
      filters: filters,
      fetchs: fetchs,
      orders: orders,
      limits: limits,
    };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathList,
      JSON.stringify(body),
      null
    );
  }

  /**
   * List data only fields
   * @param {*} fields for list
   * @param {*} filters for list
   * @param {*} fetchs for list
   * @param {*} orders for list
   * @param {*} limits for list
   */
  async listOnlyFields(fields, filters, fetchs, orders, limits) {
    const body = {
      fields: fields,
      filters: filters,
      fetchs: fetchs,
      orders: orders,
      limits: limits,
    };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathLListOnlyFields,
      JSON.stringify(body),
      null
    );
  }

  /**
   * Method for count
   * @param {*} filters for count
   * @param {*} orders for count
   * @param {*} limits for count
   */
  async count(filters, orders, limits) {
    const body = { filters: filters, orders: orders, limits: limits };
    return this.sendFetchRequest(
      this.getRealPathHost() + this.pathLCount,
      JSON.stringify(body),
      null
    );
  }

  /**
   * Method to find entity in search actions example inputs
   * @param filterFind filterFind for find in entity
   * @param mapProperties map properties in entity
   */
  async findEntitySearch(filterFind, mapProperties) {
    let entity = null;

    if (CommonsUtils.isNotNull(filterFind)) {
      let filters = [];
      filters.push(filterFind);

      let fetchs = null;

      if (
        CommonsUtils.isNotNull(mapProperties) &&
        CommonsUtils.isNotNull(mapProperties["filters"])
      ) {
        filters = mapProperties["filters"];
      }

      if (
        CommonsUtils.isNotNull(mapProperties) &&
        CommonsUtils.isNotNull(mapProperties["fetchs"])
      ) {
        fetchs = mapProperties["fetchs"];
      }

      let response = await this.list(filters, fetchs, null, new Limit(0, 1));

      if (VueContext.isElectronApp()) {
        response = JSON.parse(response);
        if (CommonsUtils.isNotNull(response) && response.statusProp === 200) {
          let body = response.jsonProp;
          const listEntities = body["data"];
          if (CommonsUtils.arrayNotNull(listEntities)) {
            entity = listEntities[0];
          }
        }
      } else {
        if (CommonsUtils.isNotNull(response) && response.status === 200) {
          let body = await response.json();
          const listEntities = body["data"];
          if (CommonsUtils.arrayNotNull(listEntities)) {
            entity = listEntities[0];
          }
        }
      }
    }

    // If entity is null load new
    if (CommonsUtils.isNull(entity)) {
      let response = await this.loadNew();
      if (VueContext.isElectronApp()) {
        response = JSON.parse(response);
        if (CommonsUtils.isNotNull(response) && response.statusProp === 200) {
          let body = response.jsonProp;
          entity = body["data"];
        }
      } else {
        if (CommonsUtils.isNotNull(response) && response.status === 200) {
          let body = await response.json();
          entity = body["data"];
        }
      }
    }

    return entity;
  }
}
